import { TabTree } from "@tab-edit/model";
import { ASTFragmentCursor } from "./cursors/node_cursor";
import { ASTCursor } from "./cursors/tree_cursor";
import { ASTFragment } from "./fragment";
import { ASTNode } from "./nodes/ast-node";

export class TabAST extends TabTree<ASTFragment> {
    get cursor() {
        return ASTCursor.from(this.fragments);
    }

    /// Iterate over the tree and its children in an in-order fashion
    /// calling the spec.enter() function whenever a node is entered, and 
    /// spec.leave() when we leave a node. When enter returns false, that 
    /// node will not have its children iterated over (or leave called).
    iterate(spec: IteratorSpec) {
        this.iterateHelper(spec, this.cursor);
    }

    private iterateHelper(spec: IteratorSpec, cursor: ASTCursor) {
        let explore: boolean | undefined;
        do {
            explore = spec.enter(cursor.node)===false ? false : true;
            if (explore===false) continue;
            if (cursor.firstChild()) {
                this.iterateHelper(spec, cursor);
                cursor.parent();
            }
            if (spec.leave) spec.leave(cursor.node);
        }while (cursor.nextSibling());
    }

    toString() {
        let str = "TabAST("
        let firstFragment = true;
        for (let fragment of this.fragments) {
            if(!firstFragment) str += ",";
            firstFragment = false
            str += new ASTFragmentCursor(fragment).printTree() 
        }
        str += ")"
        return str;
    }
}



type IteratorSpec = {
    enter: (
        node: ASTNode
    ) => false | void,
    leave?: (
        node: ASTNode
    ) => void,
    from?: number,
    to?: number
};